import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { departmentOfTrainings } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    nameEn: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formDepartmentTraining' })
class FormSystemTrainingPage extends Component {
  state = {
    departmentTrainingId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ departmentTrainingId: match.params.id });
      this.getDepartmentID(match.params.id);
    }
  };

  getDepartmentID = code => {
    const departmentmap = departmentOfTrainings.filter(department => department.code === code)[0];
    const initialValue = handleInitialValue(departmentmap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { departmentTrainingId } = this.state;
    return (
      <Card title={departmentTrainingId ? 'Cập nhật khoa đào tạo' : 'Thêm khoa đào tạo'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Mã khoa" {...formItemLayout}>
              {getFieldDecorator('code', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên khoa" hasFeedback>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên khoa Tiếng anh" hasFeedback>
              {getFieldDecorator('nameEn', {
                initialValue: initialValue.nameEn,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#department-training/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card >
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSystemTrainingPage {...props} />
  </Page>
));



